<template>
    <v-container>
        <v-row class="mt-5" justify="center">
            <v-col cols="6">
                <h2>Создание новой новости</h2>
                <v-form>
                    <v-text-field
                            label="Заголовок"
                            v-model="title"
                            :rules="[v => !!v || 'Обязательное поле']"
                            required
                    ></v-text-field>
                    <v-textarea
                            v-model="text"
                            label="Текст"
                    ></v-textarea>
                    <img v-if="show_upload_image" class="image" :src="getUploadImageLink()" alt="">
                    <v-file-input
                            v-model="image"
                            ref="file"
                            :rules="rules"
                            accept="image/png, image/jpeg, image/bmp"
                            placeholder="Выбрать фото"
                            prepend-icon="mdi-camera"
                            label="Изображение"
                            class="file mt-5"
                            @change="handleFileUpload"
                            outlined
                            name="image"
                    ></v-file-input>
                    <div>
                        <v-btn @click="submit" :disabled="!title || !text || uploading" class="primary mt-5">Добавить новость</v-btn>
                    </div>
                </v-form>
            </v-col>
        </v-row>
    </v-container>
</template>

<script>
    import {mapActions} from 'vuex'

    export default {
        name: "EditArticle",
        data: () => ({
            uploading: false,
            show_upload_image: false,
            image: null,
            title: null,
            text: null,
            file: '',
            rules: [
                value => !value || value.size < 2000000 || 'Размер изображения не должен превышать 2 MB!',
            ],
        }),
        methods: {
            ...mapActions(['setAlert']),
            getUploadImageLink() {
                return URL.createObjectURL(this.image)
            },
            handleFileUpload() {
                this.show_upload_image = true
            },
            submit() {
                this.uploading = true
                const formData = new FormData()
                formData.append('image', this.image)
                formData.append('title', this.title)
                formData.append('text', this.text)
                this.axios({
                    url: `${process.env.VUE_APP_SERVER_URL}api/v1/news`,
                    method: 'POST',
                    data: formData,
                    headers: {
                        'Content-Type': 'multipart/form-data'
                    }
                }).then(() => {
                    this.setAlert({
                        active: true,
                        color: 'success',
                        text: 'Новость добавлена'
                    })
                    this.$router.push('/news')
                    this.uploading = false
                }).catch(err => {
                    this.setAlert({
                        active: true,
                        color: 'error',
                        text: err.response.data
                    })
                    this.uploading = false
                })
            }
        },
        mounted() {
        }
    }
</script>

<style scoped>
    .image {
        max-width: 100%;
    }
</style>